/*Estilo de bienvenida*/

  #setTiempo{
      position: absolute;
      width: 100%;
      top: 50%;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
      text-align: center;
      font-family: 'Quicksand', sans-serif;
  }

#setTiempo h1{
    -webkit-align-items: center;
            align-items: center;
    margin: 2px;
    padding: 0px;
    font-size:3.5vw;
    color: #393e46;
}

#setTiempo p{
    margin: 2px;
    font-size: 2vw;
    color: #393e46;
}

#setTiempo div{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    margin: 20px auto;
    width: 40%;
}

/*Estilo de los botones*/
#setTiempo button{
    width: 10vw;
    height: 3.5vw;
    font-size: 2.5vw;
    background-color: rgba(255, 255, 255, 0);
    border: solid .5px rgba(255, 255, 255, 0);
    box-shadow: .5px .5px 10px rgb(158, 152, 152);
    border-radius: 5px;
    color: #393e46;
    cursor: pointer;
    font-family: 'Quicksand', sans-serif;
}


#setTiempo button:active{
   border: rgb(101, 170, 197) solid 3px;
   box-shadow: 1px 1px 20px rgb(101, 170, 197);
}

#setTiempo button:hover{
    background-color: rgb(101, 170, 197);
    color: white;
}

#setTiempo button:focus{
    outline: none;
}

